import {
	mapState
} from 'vuex';
const recorderManager = uni.getRecorderManager();
const innerAudioContext = uni.createInnerAudioContext();
let time1 = '';
import qiniuUploader from '../../utils/qiniuUploader.js'; //七牛sdk
import request from '../../utils/request.js';
const app = getApp();

export default {
	data() {
		return {
			id: '',
			imgArr: [],
			shoeboxImgArr: [],
			repairImgArr: [],
			shoeboxImgResArr: [],
			repairImgResArr: [],
			resultImgArr: [],
			qiniuToken: '',
			is_value: '',
			voice: '',
			is_recode: false,
			recode_load: false,
			is_recode_end: false,
			time: 0,
			result_time: '--:--',
			isplay: false,
			remarks: '',
			checked: [],
			totalPrice: 0,
			scheme: [],
			items: [
				{
					value: 'is_xi',
					name: '清洗',
					checked: true
				},
				{
					value: 'is_xiu',
					name: '修复'
				}
			]
		}
	},
	onLoad() {
		request('/api/auth/getQiniuToken', 'POST', {}).then(res => {
			console.log(res);
			this.qiniuToken = res.data;
		});
	},
	computed: {
		...mapState(['jsName', 'shapecode', 'jsId'])
	},
	methods: {
		checkboxChange: function(e) {
			let items = this.items,
				values = e.detail.value;
			for (let i = 0, lenI = items.length; i < lenI; ++i) {
				const item = items[i];
				if (values.includes(item.value)) {
					this.$set(item, 'checked', true);
				} else {
					this.$set(item, 'checked', false);
				}
			}
		},
		// 是否播放
		isPlayImg(isplay) {
			const ext = isplay ? "gif" : "png";
			let path = "../../static/images/play_bg.";
			const fullPath = path + ext;
			return fullPath;
		},
		// 添加鞋盒正面照图片
		add_shoeboxImg() {
			const that = this;
			let imgArr = this.shoeboxImgArr;
			console.log(imgArr);
			uni.chooseImage({
				sizeType: ['original', 'compressed'],
				sourceType: ['album', 'camera'],
				success(res) {
					console.log(res, that);
					that.shoeboxImgArr = imgArr.concat(res.tempFilePaths);
				}
			})
		},
		// 添加修复或不可修复的图片
		add_img() {
			let that = this;
			let imgArr = that.imgArr;
			uni.chooseImage({
				sizeType: ['original', 'compressed'],
				sourceType: ['album', 'camera'],
				success(res) {
					that.imgArr = imgArr.concat(res.tempFilePaths);
				}
			})
		},
		// 删除图片
		deteleImg(e) {
			let index = e.currentTarget.dataset.index;
			let arr = this.imgArr;
			arr.splice(index, 1)
			this.imgArr = arr
		},
		// 添加备注
		add_remarks(e) {
			let value = e.detail.value;
			this.remarks = value;
		},
		// 判断是否开始录音
		isRecode(is_recode) {
			if (is_recode) {
				this.stopRecode();
			} else {
				this.startRecode();
			}
		},
		// 开始录音
		startRecode() {
			console.log(11);
			const options = {
				duration: 10000, //指定录音的时长，单位 ms
				sampleRate: 16000, //采样率
				numberOfChannels: 1, //录音通道数
				encodeBitRate: 96000, //编码码率
				format: 'mp3', //音频格式，有效值 aac/mp3
				frameSize: 50, //指定帧大小，单位 KB
			}
			//开始录音
			recorderManager.start(options);
			recorderManager.onStart(() => {
				console.log('recorder start')
			});
			//错误回调
			recorderManager.onError((res) => {
				console.log(res);
			})
			this.recode_load = true;
			this.is_recode = true;
		},
		// 结束录音
		stopRecode() {
			console.log(22);
			let that = this;
			//停止录音
			recorderManager.stop();
			recorderManager.onStop((res) => {
				console.log(res)
				this.time = Math.round((res.duration / 1000));
				this.is_recode = false;
				this.voice = res.tempFilePath;
				this.is_recode_end = true;
				this.recode_load = false;
				console.log('停止录音', res.tempFilePath)
			})
		},
		// 播放录音
		playRecode() {
			let that = this;
			let time = this.time;
			innerAudioContext.src = that.voice;
			innerAudioContext.play();
			let num = 0;
			this.isplay = true
			time1 = setInterval(() => {
				num++
				console.log(time)
				if (num > time) {
					clearInterval(time1);
					innerAudioContext.stop();
					that.isplay = false
				} else {
					let result_time = parseInt(num / 60) + ":" + parseInt((parseFloat(num / 60) -
						parseInt(num / 60)) * 60);
					this.result_time = result_time;
				}
			}, 1000)
		},
		// 停止播放
		stop_play() {
			clearInterval(time1);
			innerAudioContext.stop();
			this.isplay = false
		},
		// 删除音频
		remove_record() {
			console.log(11);
			this.voice = ''
			this.is_recode = false
			this.recode_load = false
			this.is_recode_end = false
			this.time = 0
			this.result_time = '--:--'
			this.isplay = false
		},
		// 上传图片到七牛
		upImgs(imgArr, qiuniuToken) {
			let that = this;
			let resultImgArr = [];
			return new Promise((resolve, reject) => {
				imgArr.forEach(item => {
					var imgName = item.substr(30, 50);
					qiniuUploader.upload(item, res => {
						resultImgArr.push(res.imageURL);
						that.resultImgArr
						if (resultImgArr.length == imgArr.length) {
							resolve(resultImgArr)
						}
					}, error => {
						reject(error)
					}, {
						region: 'NCN',
						uploadURL: 'https://upload-z1.qiniup.com',
						domain: app.globalData.qiniuImgUrl,
						key: 'uploads/wash/' + imgName,
						uptoken: qiuniuToken
					})
				})
			})
		},
		// 上传录音
		upRecord(file, qiuniuToken) {
			let that = this;
			return new Promise((resolve, reject) => {
				var recordName = file.substr(30, 50);
				qiniuUploader.upload(file, res => {
					resolve(res)
				}, error => {
					reject(error)
				}, {
					region: 'NCN',
					uploadURL: 'https://upload-z1.qiniup.com',
					domain: app.globalData.qiniuImgUrl,
					key: 'uploads/record/' + recordName,
					uptoken: qiuniuToken
				})
			})
		},
		// 重新选择
		is_return() {
			uni.redirectTo({
				url: '../check/check',
			})
		},
		// 提交按钮
		is_submit() {
			uni.showLoading({
				mask: true,
				title: "加载中..."
			});
			let sound = '';
			if (this.imgArr.length == 0) {
				uni.hideLoading()
				uni.showToast({
					title: '至少添加一张瑕疵图片',
					icon: "none"
				});
				return
			}
			if (this.shapecode === "") {
				uni.hideLoading()
				uni.showToast({
					title: '条形码为必填项',
					icon: "none"
				});
				return
			}
			let that = this;
			let type_s = {};
			for (let i = 0; i < this.items.length; i++) {
				if (this.items[i].checked) {
					type_s[[this.items[i].value]] = 1;
				}
			}
			if (that.voice != "") {
				that.upRecord(that.voice, that.qiniuToken).then(res => {
					sound = res.imageURL;
					that.upImgs(that.imgArr, that.qiniuToken).then((original_image) => {
						console.log(original_image);
						that.upImgs(this.shoeboxImgArr, that.qiniuToken).then(shoes_image => {
							console.log(shoes_image);
							request("/api/auth/order", "POST", {
								consignor_id: that.jsId,
								bar_id: that.shapecode,
								original_image,
								shoes_image,
								remarks: that.remarks,
								sound,
								sound_length: this.time,
								is_xi: type_s['is_xi'],
								is_xiu: type_s['is_xiu']
							}).then(res => {
								uni.hideLoading();
								uni.showToast({
									title: res.data.message,
									icon: "none",
									duration: 1000,
									complete() {
										if (res.data.status == 200) {
											uni.showToast({
												title: '添加完成',
												icon: 'none'
											});
											setTimeout(() => {
												uni.navigateTo({
													url: "../sweep/sweep"
												});
											}, 1000);
										}
									}
								})
							});
						}).catch(error => {
							uni.hideLoading();
							uni.showToast({
								title: '图片上传失败',
								icon: "none"
							})
						});
					}).catch(error => {
						uni.hideLoading();
						uni.showToast({
							title: '图片上传失败',
							icon: "none"
						})
					})
				}).catch((res) => {
					uni.hideLoading()
					uni.showToast({
						title: '录音上传失败',
						icon: "none"
					})
				})
			} else {
				that.upImgs(that.imgArr, that.qiniuToken).then((original_image) => {
					console.log(original_image);
					that.upImgs(this.shoeboxImgArr, that.qiniuToken).then(shoes_image => {
						console.log(shoes_image);
						request("/api/auth/order", "POST", {
							consignor_id: that.jsId,
							bar_id: that.shapecode,
							original_image,
							shoes_image,
							remarks: that.remarks,
							sound,
							is_xi: type_s['is_xi'],
							is_xiu: type_s['is_xiu']
						}).then(res => {
							uni.hideLoading();
							uni.showToast({
								title: res.data.message,
								icon: "none",
								duration: 1000,
								complete() {
									uni.showToast({
										title: '添加完成',
										icon: 'none'
									});
									setTimeout(() => {
										uni.navigateTo({
											url: "../sweep/sweep"
										});
									}, 1000);
								}
							})
						});
					}).catch(error => {
						uni.hideLoading();
						uni.showToast({
							title: '图片上传失败',
							icon: "none"
						})
					});
				}).catch(error => {
					uni.hideLoading();
					uni.showToast({
						title: '图片上传失败',
						icon: "none"
					})
				})
			}
		},
		// 不可修复
		no_submit() {
			uni.showLoading({
				mask: true,
				title: "加载中..."
			});
			let sound = '';
			if (this.imgArr.length == 0) {
				uni.hideLoading()
				uni.showToast({
					title: '至少添加一张瑕疵图片',
					icon: "none"
				});
				return
			}
			if (this.shapecode === "") {
				uni.hideLoading()
				uni.showToast({
					title: '条形码为必填项',
					icon: "none"
				});
				return
			}
			let that = this;
			let type_s = {};
			for (let i = 0; i < this.items.length; i++) {
				if (this.items[i].checked) {
					type_s[[this.items[i].value]] = 1;
				}
			}
			if (that.voice != "") {
				that.upRecord(that.voice, that.qiniuToken).then(res => {
					sound = res.imageURL;
					that.upImgs(that.imgArr, that.qiniuToken).then((original_image) => {
						console.log(original_image);
						that.upImgs(this.shoeboxImgArr, that.qiniuToken).then(shoes_image => {
							console.log(shoes_image);
							const unrepair_image = shoes_image;
							request("/api/auth/order", "POST", {
								consignor_id: that.jsId,
								bar_id: that.shapecode,
								original_image,
								shoes_image,
								remarks: that.remarks,
								unrepair_image,
								sound,
								sound_length: this.time,
								is_xi: type_s['is_xi'],
								is_xiu: type_s['is_xiu'],
								states: 5
							}).then(res => {
								uni.hideLoading();
								uni.showToast({
									title: res.data.message,
									icon: "none",
									duration: 1000,
									complete() {
										if (res.data.status == 200) {
											uni.showToast({
												title: '添加完成',
												icon: 'none'
											});
											setTimeout(() => {
												uni.navigateTo({
													url: "../sweep/sweep"
												});
											}, 1000);
										}
									}
								})
							});
						}).catch(error => {
							uni.hideLoading();
							uni.showToast({
								title: '图片上传失败',
								icon: "none"
							})
						});
					}).catch(error => {
						uni.hideLoading();
						uni.showToast({
							title: '图片上传失败',
							icon: "none"
						})
					})
				}).catch((res) => {
					uni.hideLoading()
					uni.showToast({
						title: '录音上传失败',
						icon: "none"
					})
				})
			} else {
				that.upImgs(that.imgArr, that.qiniuToken).then((original_image) => {
					console.log(original_image);
					that.upImgs(this.shoeboxImgArr, that.qiniuToken).then(shoes_image => {
						console.log(shoes_image);
						const unrepair_image = shoes_image;
						request("/api/auth/order", "POST", {
							consignor_id: that.jsId,
							bar_id: that.shapecode,
							original_image,
							unrepair_image,
							shoes_image,
							remarks: that.remarks,
							sound,
							is_xi: type_s['is_xi'],
							is_xiu: type_s['is_xiu'],
							states: 5
						}).then(res => {
							uni.hideLoading();
							uni.showToast({
								title: res.data.message,
								icon: "none",
								duration: 1000,
								complete() {
									uni.showToast({
										title: '添加完成',
										icon: 'none'
									});
									setTimeout(() => {
										uni.navigateTo({
											url: "../sweep/sweep"
										});
									}, 1000);
								}
							})
						});
					}).catch(error => {
						uni.hideLoading();
						uni.showToast({
							title: '图片上传失败',
							icon: "none"
						})
					});
				}).catch(error => {
					uni.hideLoading();
					uni.showToast({
						title: '图片上传失败',
						icon: "none"
					})
				})
			}
		}
	}
}
